import React, { Component } from 'react';
import './NewTodo.css';

class NewTodo extends Component {
  constructor(props){
    super(props);
    this.state ={
      todos: [],
      value:'', };
    }
    render() {
      return (
        <div id="NewTodo">
        <h1> Add a list item</h1>
        <p>Start adding now! </p>
        <form className="newItem" onSubmit={this.props.add}>
        <p>New item:
        <br/>
        <input id="inputText" type="text" placeholder=" add new list item here"
        value={this.props.value} onChange={this.props.handleChange}/>
        </p>
        <input id="submit" type="submit" onSubmit={this.props.add} value="submit"/>
        </form>
        </div>
      );
    }
  }

  export default NewTodo;
