import React, { Component } from 'react';
import './Todo.css';
import square from './square.png';
import check from './check.png';


class Todo extends Component {
  constructor(props) {
    super(props);
    this.state ={
      value:'',
      todos: [] };

    }

    render() {
      const self = this;
      var className = "todo";
      if (self.props.completed) {
        className = "todo completed";
      }
      return (
        <li id={this.props.id}>
        <img src={this.props.completed? check:square}   alt="box" onClick={this.props.completeFunc}></img>
        <p className={className}>{this.props.text}</p>
        <input id="delete" type="submit" value="Delete" onClick={this.props.deleteFunc}/>
        </li>
      );
    }
  }

  export default Todo;
