import React, { Component } from 'react';
import './App.css';
import Todo from './Todo';
import NewTodo from './NewTodo';
import square from './square.png';
import check from './check.png';

class App extends Component {
  constructor(props){
    super(props);
    this.state ={
      todos: [],
      value:'', };

      this.handleChange = this.handleChange.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);
      this.sortFunction = this.sortFunction.bind(this);
      this.completeFunction = this.completeFunction.bind(this);
      this.deleteFunction = this.deleteFunction.bind(this);
    }

    completeFunction(event){
      event.preventDefault();
      const self = this;

      var id = event.target.parentNode.id; //gets list item
      var textPara = event.target.parentNode.getElementsByTagName('P');
      var checkbox = event.target.parentNode.getElementsByTagName('IMG');

      var xhttp3 = new XMLHttpRequest();
      var data = {
        completed: true
      };
      var todo = [];
      // Response handler
      xhttp3.onreadystatechange = function() {
        // Wait for readyState = 4 & 200 response
        if (this.readyState === 4 && this.status === 200) {
          // parse JSON response
          todo = JSON.parse(this.responseText);
          textPara[0].classList.add('completed');
          checkbox[0].setAttribute("src", check);
          self.setState({completed:true});
          console.log(todo);
        } else if (this.readyState === 4) {
          // this.status !== 200, error from server
          console.log(this.responseText);
        }
      };
      xhttp3.open("PUT", "https://cse204.work/todos/"+id, true);
      xhttp3.setRequestHeader("Content-type", "application/json");
      xhttp3.setRequestHeader("x-api-key", "6c0961-a3464e-706c1d-da09cf-ed7376");
      xhttp3.send(JSON.stringify(data));
    }

    deleteFunction(event){
      event.preventDefault();
      const self = this;
      var id = event.target.parentNode.id;
      var xhttp4 = new XMLHttpRequest();
      // Response handler
      xhttp4.onreadystatechange = function() {
        // Wait for readyState = 4 & 200 response
        if (this.readyState === 4 && this.status === 200) {
          // parse JSON response
          var todo = JSON.parse(this.responseText);
          console.log(todo);
        } else if (this.readyState === 4) {
          // this.status !== 200, error from server
          console.log(this.responseText);
        }
      };

      xhttp4.open("DELETE", "https://cse204.work/todos/"+id, true);
      xhttp4.setRequestHeader("Content-type", "application/json");
      xhttp4.setRequestHeader("x-api-key", "6c0961-a3464e-706c1d-da09cf-ed7376");
      xhttp4.send();

      const remainingTodos = self.state.todos.filter((todo) => {
        // Looping through all todos, if the id of the current todo DOES NOT equal the id of the todo we want to delete, keep it
        if (todo.id !== id) {
          return todo;
        }
      });
      // Update state with filtered list using this.setState();
      self.setState({todos:remainingTodos});
    }

    componentDidMount() {
      const self = this;
      var xhttp = new XMLHttpRequest();
      var todos;
      //listing items
      xhttp.onreadystatechange = function() {
        if (this.readyState === 4 && this.status === 200) {
          todos = JSON.parse(this.responseText);
          console.log(todos);
          self.setState({todos:todos});
        }
        else if (this.readyState===4) {
          console.log(this.responseText);
        }
      };
      xhttp.open("GET", "https://cse204.work/todos", true);
      xhttp.setRequestHeader("x-api-key","6c0961-a3464e-706c1d-da09cf-ed7376");
      xhttp.send();
    }

    handleChange(event) {
      event.preventDefault();
      this.setState({value: event.target.value});
    }

    handleSubmit(event) { //make the list item
      event.preventDefault();
      const self = this;
      // Initalize AJAX Request
      var xhttp2 = new XMLHttpRequest();
      var data = {
        text:self.state.value,
      };
      // Response handler
      xhttp2.onreadystatechange = function() {
        // Wait for readyState = 4 & 200 response
        if (this.readyState === 4 && this.status === 200) {
          // parse JSON response
          var todo = JSON.parse(this.responseText);
          console.log(todo);
          self.setState({ todos:[...self.state.todos, todo], value: '' });
        } else if (this.readyState === 4) {
          // this.status !== 200, error from server
          console.log(this.responseText);
        }  };
        xhttp2.open("POST", "https://cse204.work/todos", true);

        xhttp2.setRequestHeader("Content-type", "application/json");
        xhttp2.setRequestHeader("x-api-key", "6c0961-a3464e-706c1d-da09cf-ed7376");
        xhttp2.send(JSON.stringify(data));
      }

      sortFunction() {
        const self = this;
        const sortedArray = self.state.todos.sort(function (a, b) {
          return a.text.localeCompare(b.text);
        })
        console.log(this.state.todos);
        self.setState({ todos:sortedArray});
      }

      render() {
        return (
          <div id="body">

          <div id="notebook">
          <h1>The To Do List</h1>
          <p>Add and delete items from your list!</p>

          <ul>
          <input id="sort" type="button" value="Sort Alphabetically!" onClick={this.sortFunction}/>

          {this.state.todos.map((todo) =>
            <Todo
            id={todo.id}
            key={todo.id}
            text={todo.text}
            completed={todo.completed}
            created={todo.created}
            completeFunc={this.completeFunction}
            deleteFunc={this.deleteFunction}
            />)}

            </ul>
            </div>

            <NewTodo handleChange={this.handleChange} value={this.state.value} add={this.handleSubmit} />

            </div>
          );      }

        }

        export default App;
